(function(){

	/**
	* Contato Controller
	*
	* Controller that manages the Contato page
	*/
	angular.module('app').controller('ContatoController', [ContatoController]);

	/**
	 * Contato Controller Function
	 */
	function ContatoController() {

		var uluru = {lat: -19.972378, lng: -43.975787};
		var map = new google.maps.Map(document.getElementById('map'), {
			zoom: 11,
			center: uluru
		});
		var marker = new google.maps.Marker({
			position: uluru,
			map: map
		});

	}

})()