 (function(){

	/**
	* State configuration
	*
	* Main application module
	*/
	angular.module('app').config(['$stateProvider', '$urlRouterProvider', stateConfig]);

	/**
	 * State configuration function
	 *
	 * Configures all of the pages with their respective controllers, urls and states
	 * 
	 * @param  {AngularProvider} $stateProvider     Provides state manegment functions
	 * @param  {AngularProvider} $urlRouterProvider Provides url manegment functions
	 */
	function stateConfig($stateProvider, $urlRouterProvider) {

		// For any unmatched url, redirect to '/'
		$urlRouterProvider.otherwise('/');

		// Setup the states
		$stateProvider

		/**
		 * Home State
		 * 	
		 * @type {State}
		 */
		.state('home', {
			url: '/',
			templateUrl: 'home.html',
			controller: 'HomeController',
			controllerAs: 'home'
		})

		/**
		 * Contato State
		 * 	
		 * @type {State}
		 */
		.state('contato', {
			url: '/contato',
			templateUrl: 'contato.html',
			controller: 'ContatoController',
			controllerAs: 'contato'
		})

		/**
		 * sobre State
		 * 	
		 * @type {State}
		 */
		.state('sobre', {
			url: '/sobre',
			templateUrl: 'sobre.html',
			controller: 'SobreController',
			controllerAs: 'sobre'
		})

		/**
		 * produtos State
		 * 	
		 * @type {State}
		 */
		.state('produtos', {
			url: '/produtos',
			templateUrl: 'produtos.html',
			controller: 'ProdutosController',
			controllerAs: 'produtos'
		})


		/**
		 * portifolio State
		 * 	
		 * @type {State}
		 */
		.state('portifolio', {
			url: '/portifolio',
			templateUrl: 'portifolio.html',
			controller: 'PortifolioController',
			controllerAs: 'portifolio'
		})
	}

})()