(function(){

	/**
	* Body Controller
	*
	* Controller that manages the Body page
	*/
	angular.module('app').controller('BodyController', ['$mdSidenav', BodyController]);

	/**
	 * Body Controller Function
	 */
	function BodyController($mdSidenav) {

		var body = this;

		body.toggleSidnav = function () {
			$mdSidenav("sidenav").toggle()
		}

	}

})()